<template>
    <div class="cl">
        <ul>
            <li class="box" v-for="(v,i) in list" :key="i" @click="goto(v)">
                <img :src="imgname(v.img)">
                <p>{{v.text}}</p>
            </li>
        </ul>
    </div>
</template>

<script>
import router from '../router/index'

export default {
    name: 'CliTw',
    props:[ 'list'],
    data() {
        return {
            
        };
    },
    mounted() {
        
    },
    methods: {
        imgname(name){
            return require('../assets/comclass/'+name);
        },
        goto(v){
            router.push({path:'detail',query:{id:v.id,img:v.img,text:v.text}})
        }
    },
};
</script>

<style scoped>
    .cl{
        margin-top: 1rem;
        
    }
    ul{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding: 10px;
    }
    li{
        width: 28%;
        
        display: flex;
        font-weight: bold;
        font-size: .9rem;
        overflow: hidden;
        color:#80766e;
        justify-content:space-around;
        /* align-self: flex-end; */
        flex-wrap: wrap;
        margin-bottom: 10px;
    }
    img{
        width: 100%;
        display: block;
        /* border: 1px solid #000; */
        overflow: hidden;
        border-radius: 1rem;
    }
</style>