import { createRouter, createWebHistory } from 'vue-router'
import bnav from '../components/bnav.vue'
import main from '../view/main.vue'
import tab from '../components/tab.vue'
import search from '../components/search.vue'
import rec from '../components/rec.vue'
import rolltab from '../components/rolltab.vue'
import waterfall from '../components/waterfall.vue'
import car from '../components/car.vue'
import model from '../components/model.vue'
import scene from '../components/scene.vue'
import scenes from '../components/scenes.vue'
import me_top from '../components/me_top.vue'
import user from '../components/user.vue'
import me from '../components/me.vue'
import mect from '../components/mect.vue'
import search2 from '../components/search2.vue'
import clas from '../components/clas.vue'
import clases from '../components/clases.vue'
import details from '../components/details.vue'
import imgg from '../components/imgg.vue'
import error from '../components/error.vue'
import axios from '../components/axios.vue'
import login from '../components/login.vue'
import three from '../components/three.vue'


const router = createRouter({
    history: createWebHistory(),
   routes: [
        {
            path: '/bnav',
            component: bnav ,
            name: 'bnav'
        },
        {
            path: '/',
            component: main,
            name: 'main'
        },
        {
            path: '/tab',
            component: tab,
            name: 'tab'
        },
        {
            path: '/search',
            component: search,
            name: 'search'
        },
        {
            path: '/rec',
            component: rec,
            name: 'rec'
        },
        {
            path: '/rolltab',
            component: rolltab,
            name: 'rolltab'
        },
        {
            path: '/waterfall',
            component: waterfall,
            name: 'waterfall'
        },
        {
            path: '/car',
            component: car,
            name: 'car',
            redirect:'/main'
        },
        {
            path: '/model',
            component: model,
            name: 'model'
        },
        {
            path: '/scene',
            component: scene,
            name: 'scene'
        },
        {
            path: '/scenes',
            component: scenes,
            name: 'scenes'
        },
        {
            path: '/me_top',
            component: me_top,
            name: 'me_top'
        },
        {
            path: '/user',
            component: user,
            name: 'user'
        },
        {
            path: '/me',
            component: me,
            name: 'me'
        },
        {
            path: '/mect',
            component: mect,
            name: 'mect'
        },
        {
            path: '/search2',
            component: search2,
            name: 'search2'
        },
        {
            path: '/clas',
            component: clas,
            name: 'clas'
        },
        {
            path: '/clases',
            component: clases,
            name: 'clases'
        },
        {
            path: '/details',
            component: details,
            name: 'details'
        },
        {
            path: '/imgg',
            component: imgg,
            name: 'imgg'
        },
        {
            path: '/error',
            component: error,
            name: 'error',
            alias:'/cuowu'
        },
        {
            path: '/:pathMatch(.*)*',
            component:error
        },
        {
            path: '/axios',
            component: axios,
            name: 'axios'
        },
        {
            path: '/login',
            component: login,
            name: 'login'
        },
        {
            path: '/three',
            component: three,
            name: 'three'
        },
    ]
})
export default router