import { createStore } from 'vuex'
export default createStore({
    state: {
        count: 0,
        c:true
    },
    mutations: {//修改状态state的方法
        add(sta){
            if (sta.c){
                sta.count++;
                sta.c=false
            }
            else{
                sta.count--;
                sta.c=true;
            }
                
        }
    },
    actions: {

    },
    getters: { 
       
    }
})