<template>
  <div class="scen">
        <h3>{{title}}</h3>
        <ul>
            <li class="box" v-for="(v,i) in list" :key="i" @click="goto(v)">
                <img :src="imgname(v.img)">
                <p>{{v.text}}</p>
            </li>
        </ul>
  </div>
</template>

<script>
import router from '../router/index'

export default {
    name: 'CliTw',
    props:[ 'title','list'],
    data() {
        return {
            
        };
    },
    mounted() {
        
    },
    methods: {
        imgname(name){
            return require('../assets/scene/'+name);
        },
        goto(v){
            router.push({path:'detail',query:{id:v.id,img:v.img,text:v.text}})
        }
    },
};
</script>

<style scoped>

    ul{
        display: flex;
        width: 100%;
        flex-wrap: wrap;
        overflow: hidden;
        /* margin-bottom: 4rem; */
    }
    li{
        width: 45%;
        height: 45vw;
        margin: 0 auto;
        border-radius: 5px;
        display: flex;
        align-items: flex-end;
        font-weight: bold;
        font-size: 1.4rem;
        overflow: hidden;
        color: white;
        /* border: 1px solid #000; */
        margin-top: 10px;
    }
    li>p{
        display: flex;
        position: absolute;
        margin-bottom: .8rem;
        margin-left: .5rem;
        z-index: 7;
    }
    img{
        width: 100%;
    }
    h3{
        margin-left:  10px;
        margin-bottom: 15px;
        margin-top: 4rem;
    }
</style>