<template>
  <ul>
      <li v-for="(v,i) in car_list" :key="i" @click="goto(v)">
          <div>
            <img :src="imgname(v.img)" alt="">
          </div>
          <p class="text1">{{v.text1}}</p>

          <div class="tagg1">
              <van-tag color="#ffe1e1" text-color="#ec0000" class="tagg1">{{v.tagg1}}</van-tag>
              <van-tag color="#eee" text-color="#000" class="tagg2">{{v.tagg2}}</van-tag>
          </div>
          
          <div class="text2-3">
            <span class="text2"><b>{{v.text2}}</b><span class="text2_1"><del>{{v.text2_1}}</del></span></span>
            <span class="text3">{{v.text3}}</span>
          </div>
          
      </li>
  </ul>
</template>

<script>
import router from '../router/index'
import { Tag } from 'vant';

export default {
    name: 'PnameMain',
    props:['car_list'],
    components: { [Tag.name]: Tag },
    data() {
        return {
            
        };
    },
    mounted() {
        
    },
    methods: {
        imgname(name){
            return require('../assets/car/'+name)
        },
        goto(v){
            router.push({
                path:'details',query:{id:v.id,img:v.img,text1:v.text1,tagg1:v.tagg1,tagg2:v.tagg2,text2:v.text2,text2_1:v.text2_1,text3:v.text3}
            });
        }
    },
}
</script>

<style scoped>
    ul{
        width: 95%;
        /* display: flex;
        flex-wrap: wrap;
        justify-content: space-around; */
        margin: 20px auto;
        /* 瀑布流 */
        columns: 2;
        column-gap: 40px;
    }
    li{
        flex-wrap: wrap;
        /* border: 1px solid #000; */
        height: 100%;
        width: 100%;
        break-inside: avoid;
        margin: 0 0 5px;
    }
    div{
        height: 20%;
        overflow: hidden;
        border-radius: .5rem;
        /* border: 1px solid #000; */
    }
    img{
        width: 100%;
        height: 100%;
    }
    .text1{
        font-weight: bold;
        font-size: 1rem;
        padding-top: .3rem;
    }
    .text2{
        /* font-weight: bold; */
        color: rgb(230, 9, 9);
        font-size: 1.1rem;
    }
    .text3,.text2_1{
        color: #bbb;
        font-size: .8rem;
    }
    .van-tag{
        margin-left: 5px;
    }
    .text2,.text3{
        display: inline-block;
        padding-top: .5rem;
    }
    .text2-3{
        display: flex;
        justify-content:space-between;
    }
</style>