<template>
    <div class="rec">
        <ul>
            <li class="box" v-for="(v,i) in list" :key="i" @click="goto(v)">
                <img :src="imgname(v.img)">
                <p>{{v.text}}</p>
            </li>
        </ul>
    </div>
</template>

<script>
import router from '../router/index'

export default {
    name: 'CliTw',
    props:[ 'list'],
    data() {
        return {
            
        };
    },
    mounted() {
        
    },
    methods: {
        imgname(name){
            return require('../assets/rec/'+name);
        },
        goto(v){
            router.push({path:'detail',query:{id:v.id,img:v.img,text:v.text}})
        }
    },
};
</script>

<style lang="css" scoped>
 .rec{
        margin-top: 4rem;
    }
    ul{
        display: flex;
        width: 100%;
        /* border: 1px solid #000; */
        height: 70vw;
        flex-wrap: wrap;
    }
    li{
        width: 30%;
        height: 40%;
        margin: 0 auto;
        /* border: 1px solid #000; */
        border-radius: 5px;
        /* padding: .5rem; */
        
        display: flex;
        -webkit-align-items: flex-end;
        align-items: flex-end;
        font-weight: bold;
        font-size: 1.4rem;
        overflow: hidden;
        color: white;
    }
    li>p{
        display: flex;
        position: absolute;
        margin-bottom: .8rem;
        margin-left: .5rem;
        z-index: 7;
    }
    ul li:last-child{
        border: 1px solid #ccc;
        font-weight: normal;
        font-size: 1.2rem;
        align-items:stretch;
        position: relative;
        overflow: hidden;
    }
    ul li:last-child>img+p{
        position: absolute;
        top: 37%;
        left: 25%;
        color: black;
        font-size: 1.1rem;
    }
    ul li:last-child>img{
        filter: blur(1px);
        width: 100%;
        height: 100%;
    }
    img{
        width: 100%;
    }
    
</style>