<template>
  <div class="box">
    <ul>
      <li v-for="(v, i) in list" :key="i">
        <img :src="v.pic" alt="没得了" />
        <p>{{ v.title }}</p>
      </li>
    </ul>
  </div>
</template>
<script>
import {
 ref
} from "vue";

export default {
  setup() {
    var list = ref("");
    // var { ctx } = getCurrentInstance(); //ctx固定 解构赋值
    const axios = require("axios").default;
    axios
      .get(
        "http://rap2api.taobao.org/app/mock/285253/scene"
      )
      .then(function (response) {
        // 处理成功情况
        list.value = response.data;
        // console.log(response);
      })
      .catch(function (error) {
        // 处理错误情况
        console.log(error);
      })
      .then(function () {
        // 总是会执行
      });
    return {
      list,
    };
  },
};
</script>
<style scoped>
  ul{
        display: flex;
        width: 100%;
        flex-wrap: wrap;
        overflow: hidden;
        margin-bottom: 4rem;
    }
    li{
        width: 45%;
        height: 45vw;
        margin: 0 auto;
        border-radius: 5px;
        display: flex;
        align-items: flex-end;
        font-weight: bold;
        font-size: 1.4rem;
        overflow: hidden;
        color: white;
        /* border: 1px solid #000; */
        margin-top: 10px;
    }
    li>p{
        display: flex;
        position: absolute;
        margin-bottom: .8rem;
        margin-left: .5rem;
        z-index: 7;
    }
    img{
        width: 100%;
        /* height: 200%; */
    }
</style>